* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

ul{
  list-style: none;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.flag{
  width: 20px;
  height: auto;
}

.bigflag{
  width: 200px;
  height: auto;
  display: block;
  padding-left:40px;
}

.country-info{
  width: 500px;
}

.country-info__name{
padding-left: 40px;
}



